"use client"
import React, { useEffect, useState } from 'react';
import Image from 'next/image';
import Navbar from '@/components/navbar';
import GradientBackground from '@/components/GradientBackground';
import { motion } from 'framer-motion';
import ButtonSvg from '@/public/Buttonsvg';

const Home: React.FC = () => {
  const words = ['Secure.', 'Protect.', 'Analyze.'];
  const [currentWordIndex, setCurrentWordIndex] = useState(0);
  const [showCursor, setShowCursor] = useState(true);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentWordIndex((prevIndex) => (prevIndex + 1) % words.length);
    }, 2000);

    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    const cursorBlink = setInterval(() => {
      setShowCursor((prev) => !prev);
    }, 500);

    return () => clearInterval(cursorBlink);
  }, []);

  const handleTryMe = () => {
    window.location.href = '/analyze';
  };

  return (
    <div className="relative min-h-screen flex flex-col text-center">
      {/* Background layer */}
      <div className="absolute inset-0 z-0">
        <GradientBackground />
      </div>
      
      {/* Navbar */}
      <div className="relative z-10 w-full">
        <Navbar />
      </div>
      
      {/* Content layer */}
      <div className="relative z-10 flex flex-col items-center justify-center flex-grow">
        <main className="w-full max-w-4xl text-center px-4">
          <div className='text-5xl font-bold text-white mt-10 h-12 flex justify-center items-center'>
            <motion.span
              key={words[currentWordIndex]}
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -10 }}
              transition={{ duration: 0.5 }}
              className="mr-2"
            >
              {words[currentWordIndex]}
            </motion.span>
            {showCursor && <span className="text-white">|</span>}
          </div>
          
          <p className="text-xl text-gray-300 mt-10 mb-8 max-w-2xl mx-auto">
            Unveil the hidden layers of your text. Understand sensitivity before you share.
          </p>
          
          <motion.button 
            onClick={handleTryMe}
            className="relative inline-flex items-center justify-center h-11 transition-colors hover:text-color-1 px-7 text-n-1 hover:scale-105 transform duration-300 ease-in-out"
            whileHover={{ scale: 1.1 }}
          >
            <span className="relative z-10">Try Me</span>
            {ButtonSvg(false)}
          </motion.button>
        </main>
      </div>
    </div>
  );
};

export default Home;